import React, {Component} from 'react'
import firebase from 'firebase'
import {DB_C} from '../config.js'
import Ppl from './ppl.js'
import Pplform from './pplform'
import Searchbar from './searchbar'
import { Grid, Cell } from 'react-md'
import './nav.css'

class Ppldata extends Component{
    constructor(){
        super()

            this.dd = firebase.initializeApp(DB_C);
            this.db = this.dd.database().ref().child('pplls');
            this.addperson = this.addperson.bind(this);
            this.deletebt = this.deletebt.bind(this);
            this.searching = this.searching.bind(this);
            
                this.state ={
                    pplls: [],
                    search:""
                }
    }
    
        componentWillMount(){
            const previusPerson = this.state.pplls

            this.db.on('child_added', snap => {
                previusPerson.push({
                    id: snap.key,
                    name: snap.val().name,
                    age: snap.val().age,
                    url: snap.val().url,
                    other: snap.val().other,
                    gender: snap.val().gender,
                })
                    this.setState({
                        pplls: previusPerson
                    })
            })
            this.db.on('child_removed', snap => {
                for(var i=0; i< previusPerson.length; i++){
                    if(previusPerson[i].id === snap.key){
                        previusPerson.splice(i,1)
                    }
                }
                this.setState({
                    pplls: previusPerson
                })
            })
        }

        addperson(name, age, url, other, gender){
            if(name === "" || age === "" || other === ""){
                alert('err');
            }else if(gender === "male" && url === ''){
                this.db.push().set({ 
                    name: name,
                    age:age,
                    url:"http://serenityforge.com/template/wordpress/wp-content/uploads/2014/06/facebook-default-no-profile-pic.jpg",
                    other:other
                })
            }else if(gender === 'female' && url === ''){
                this.db.push().set({ 
                    name: name,
                    age:age,
                    url:'https://i.pinimg.com/236x/f3/61/e3/f361e30b86fee7661463bf331de5b1b1.jpg',
                    other:other
                }) 
            }else if (url !== ''){
                this.db.push().set({ 
                    name: name,
                    age:age,
                    url:url,
                    other:other
                })
            }
        }

        deletebt(id){
            this.db.child(id).remove();
        }

        searching(e){
            const newsearch = e
            this.setState({
                search: newsearch
            })
        }

    render() {
        let filteredPplls = this.state.pplls.filter(
            (index) => {
                return index.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1;
            }
        );

        return (
           <div>
               <div>
                    <Grid>
                        <Cell size={12}>
                            <div>
                                <h1>Every Body That Added Himself Here</h1>
                            </div>
                        </Cell>
                    </Grid>

                    <center>
                        <Grid> 
                            <Cell size={12}>
                                <Searchbar searching={this.searching}/>
                            </Cell>
                        </Grid>
                    </center>

                    <Grid>
                        {
                            filteredPplls.map((ppls) =>{
                                return(
                                        <Cell size={4}>
                                            <Ppl 
                                                id={ppls.id}
                                                name={ppls.name} 
                                                age={ppls.age} 
                                                url={ppls.url} 
                                                other={ppls.other} 
                                                key={ppls.id} 
                                                deletebt={this.deletebt}
                                            />
                                        </Cell>
                                )
                            })
                        }
                    </Grid>
               </div>
               <Pplform addperson={this.addperson}/>
           </div>
        );
    }
}



export default Ppldata;
