import React, { Component } from 'react';
import './pplform.css'
import { Grid, Cell } from 'react-md';

class Pplform extends Component {
    constructor(){
        super()
        this.state = {
            url:"",
            other:"",
            name:"",
            age:'',
            gender:'male',
            genderoptionMen:true,
            genderoptionWomen:false,
            display:"none",
            displayobt:"block",
            displaycbc:"block",
        }
        
        this.handleUserage = this.handleUserage.bind(this);
        this.handleUsername = this.handleUsername.bind(this);
        this.handleUserother = this.handleUserother.bind(this);
        this.handleUserurl = this.handleUserurl.bind(this);
        this.submited = this.submited.bind(this);
        this.openb = this.openb.bind(this);
        this.closebc = this.closebc.bind(this);
        this.handleUserGender = this.handleUserGender.bind(this);
    }
    
    handleUserage(e){
        this.setState({
            age: e.target.value
        })
    }

    handleUserurl(e){
        this.setState({
            url: e.target.value
        })
    }

    handleUserother(e){
        this.setState({
            other: e.target.value
        })
    }

    handleUsername(e){
        this.setState({
            name: e.target.value
        })
    }


    submited(){
        this.props.addperson(this.state.name, this.state.age, this.state.url, this.state.other, this.state.gender);
        this.setState({
            url:"",
            other:"",
            name:"",
            age:'',
            gender:'male'
        })
    }

    openb(){
        this.setState({
            display:'block',
            displayobt:'none',
            displaycbc:"block"
        })
    }

    closebc(){
        this.setState({
            display:'none',
            displayobt:'block',
            displaycbc:"none"
        })
    }

    handleUserGender(e){
        if(e.target.name === 'female'){
            this.setState({
                gender:'female',
                genderoptionMen:false,
                genderoptionWomen:true,
            })
        }else if (e.target.name === 'male'){
            this.setState({
                gender:'male',
                genderoptionMen:true,
                genderoptionWomen:false,
            })
        }
    }

  render() {

    const myForm ={ "display": this.state.display}
    const openbt = {"display":this.state.displayobt}
    const closebc = {"display":this.state.displaycbc}

        return (
        <div>
            <button className="open-button" onClick={this.openb} style={openbt}>+</button>
                <center>
                        <div className="form-popup" style={myForm}>
                            <div className="form-container">
                                <Grid>
                                    <Cell size={1}>
                                        <h2>Name:</h2>

                                        <input 
                                        type="text" 
                                        maxlength="10" 
                                        onChange={this.handleUsername} 
                                        value={this.state.name}  
                                        placeholder="name goes here" 
                                        />
                                    </Cell>
                                    <Cell size={4}>
                                        <h2>Icon Url: <p className="notreq">(not required)</p></h2>
                                        <input 
                                        type="text"  
                                        onChange={this.handleUserurl} 
                                        value={this.state.url}  
                                        placeholder="image url" 
                                        />
                                    </Cell>
                                    <Cell size={1}>
                                        <h2>Age:</h2>

                                        <input 
                                        type="number"   
                                        onChange={this.handleUserage} 
                                        value={this.state.age} 
                                        placeholder="age goes here" 
                                        />
                                    </Cell>
                                    <Cell size={4}>
                                        <h2>Other:</h2>

                                        <input 
                                        type="text"  
                                        onChange={this.handleUserother} 
                                        value={this.state.other} 
                                        placeholder="Other things you want to inform about" 
                                        />
                                    </Cell>
                                    <Cell size={2}>
                                        <h2>Gender</h2>
                                            <label class="container">Male

                                            <input 
                                            type="radio" 
                                            name="male"
                                            checked={this.state.genderoptionMen} 
                                            onChange={this.handleUserGender}
                                            />

                                            <span class="checkmark"></span>
                                            </label>

                                            <label class="container">Female

                                            <input 
                                            type="radio" 
                                            name="female"
                                            checked={this.state.genderoptionWomen}
                                            onChange={this.handleUserGender}
                                            />

                                            <span class="checkmark"></span>
                                            </label>
                                    </Cell>
                                    <Cell size={12}>
                                    <div className="twobuttons">
                                        <button onClick={this.submited} className="btn">Add</button>
                                        <button type="button" className="btn cancel" onClick={this.closebc} style={closebc}>Close</button>
                                    </div>
                                    </Cell>
                                </Grid>
                            </div>
                        </div>
                </center>
        </div>
            
            )
         }
}

export default Pplform;
