import React, { Component } from 'react';
import '../App.css';


  const stylingdiv = {
    "width": "350px",
    "border": "2px solid Black",
    "padding":"20px",
  }

class Ppl extends Component {
  constructor(props){
    super(props);
    this.name = props.name;
    this.url = props.url;
    this.age = props.age;
    this.other = props.other;
    this.id = props.id;
    this.handledeletebt = this.handledeletebt.bind(this);
  }

  handledeletebt(id){
    this.props.deletebt(id);
  }

  render() {
    return (
      <div style={stylingdiv}>
        <button className="close" onClick={() => this.handledeletebt(this.id)}>x</button>
          <center>
            
            <img src={this.url} className="pic"/>
            
            <h2 className="name">Name:{this.name}</h2>
            <br/>
            
            <h4>Age:{this.age}</h4>
            <br/>
            <hr/>
            
            <h4 className="other">Other:</h4>
            <p>{this.other}</p>
            <br/>
            <hr/>

          </center>
      </div>
    );
  }
}

export default Ppl;